<template>
	<view class="news">
		<view class="news-menu">
			<view @click="gotoTalkComment()" class="item">
				<image style="width: 30px;height: 30px;margin-bottom: 10px;" src="../../static/images/icon/comment-active.png"></image>
				<span>回复我的</span>
			</view>
			<view class="item">
				<!-- <image style="width: 30px;height: 30px;margin-bottom: 10px;" src="../../static/images/icon/公告_announcement.png"></image>
				<span>系统通知</span> -->
			</view>
			<view class="item"></view>
			<view class="item"></view>
		</view>
		<view class="warp">
			<view class="title">
				<span>关注的人：</span>
				<myXhx width="60"></myXhx>
			</view>
			<view class="talk-list">
				<talk-list-by-time  v-if="loading"  ref="talkList"></talk-list-by-time>
			</view>
		</view>
	</view>
	
</template>

<script>
	import talkListByTime from "@/components/talkListByTime/talkListByTime.vue";
	export default {
		data() {
			return {
				userId:null,
				typeId:null,
				loading:false
			}
		},
		components:{
			talkListByTime
		},
		onLoad(op) {
			//使用loading，防止props取不到值
			this.loading=0
			this.userId = op.userId
			this.typeId = op.typeId
			this.loading=1
			
		},
		//下拉刷新
		onPullDownRefresh() {
			this.$refs.talkList.reFresh().then(res=>{
				uni.stopPullDownRefresh();
			})
		},
		methods: {
			gotoTalkComment(){
				uni.navigateTo({
					url:`/pages/talkComment/talkComment?userId=${this.userId}`
				})
			}
		
		},
		//触底
		onReachBottom(){
			this.$refs.talkList.handleCurrentChange();
			console.log("触底了")
		}
	}
</script>

<style>
.news-menu{
	display: flex;
	align-items: center;
	justify-content: center;
	/* padding: 10px 0; */
	margin-top: 2px;
	margin-bottom: 15px;
	background-color: white;
}
.news-menu > .item{
	width: 187.5rpx;
	height: 187.5rpx;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
}
</style>
